@use '../base/variables' as vars;
@use '../base/mixins' as mixins;

.timeline__list {
  display: grid;
  gap: vars.$space-md;
  position: relative;
  padding-left: vars.$space-lg;
}

.timeline__list::before {
  content: '';
  position: absolute;
  left: 1rem;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(180deg, rgba(18, 24, 38, 0.12) 0%, rgba(18, 24, 38, 0.04) 100%);
}

.timeline-card {
  position: relative;
  @include mixins.card-surface;
  padding: vars.$space-lg vars.$space-md;
  display: grid;
  gap: vars.$space-sm;
  background: linear-gradient(155deg, vars.$color-surface 0%, vars.$color-surface-alt 100%);
}

.timeline-card::before {
  content: '';
  position: absolute;
  left: -1.55rem;
  top: vars.$space-md;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: vars.$color-brand;
  box-shadow: 0 0 0 5px rgba(249, 115, 22, 0.22);
}

.timeline-card--education::before {
  background-color: vars.$color-text;
  box-shadow: 0 0 0 5px rgba(18, 24, 38, 0.16);
}

.timeline-card__meta {
  display: grid;
  gap: vars.$space-3xs;
}

.timeline-card__title {
  font-size: 1.5rem;
  margin: 0;
}

.timeline-card__place {
  margin: 0;
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.14em;
  color: vars.$color-text-soft;
}

.timeline-card__date {
  font-size: 0.9rem;
  font-weight: vars.$font-weight-semibold;
  color: vars.$color-brand;
}

.timeline-card__details,
.timeline-card__summary {
  margin: 0;
  color: vars.$color-text-muted;
  line-height: 1.65;
}

.timeline-card__details {
  display: grid;
  gap: 0.55rem;
  padding-left: 1.1rem;
  list-style: disc;
}

@include mixins.respond-to(lg) {
  .timeline__list {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: vars.$space-lg;
    padding-left: 0;
  }

  .timeline__list::before {
    display: none;
  }

  .timeline-card::before {
    left: vars.$space-md * -1 + 0.35rem;
  }
}
