@use '../base/variables' as vars;
@use '../base/mixins' as mixins;

.contact__grid {
  display: grid;
  gap: vars.$space-md;
}

.contact-card {
  @include mixins.card-surface;
  padding: vars.$space-md;
  display: grid;
  gap: vars.$space-3xs;
  background: linear-gradient(180deg, vars.$color-surface 0%, vars.$color-surface-alt 100%);
  transition:
    transform vars.$transition-duration-base vars.$transition-easing-standard,
    box-shadow vars.$transition-duration-base vars.$transition-easing-standard,
    border-color vars.$transition-duration-base vars.$transition-easing-standard,
    scale vars.$transition-duration-base vars.$transition-easing-standard;
}

.contact-card:hover,
.contact-card:focus-within {
  transform: translateY(-6px) scale(1.02);
  box-shadow: vars.$shadow-hover;
  border-color: rgba(249, 115, 22, 0.28);
}

.contact-card__label {
  margin: 0;
  font-size: 0.85rem;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  color: vars.$color-text-soft;
}

.contact-card__link {
  font-size: 1.15rem;
  font-weight: vars.$font-weight-semibold;
  color: vars.$color-text;
  transition: color vars.$transition-duration-base vars.$transition-easing-standard;
}

.contact-card__link:hover,
.contact-card__link:focus-visible {
  color: vars.$color-brand;
}

@include mixins.respond-to(md) {
  .contact__grid {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}
